import React, { Component } from 'react';
import {Grid, Cell} from 'react-mdl';

class Resume extends Component {
    render() {
        return(
            <div>
                <Grid>
                    <Cell cols={4} style={{textAlign: 'center'}}>
                        <div style={{textAlign: 'center'}}>
                            <img src="https://cdn1.imggmi.com/uploads/2019/12/13/27266257b187362f1aa0ee2e6f745e2f-full.png" alt="avatar" style={{height: '200px'}} />
                        </div>
                    <h2 style={{paddingTop: '2em'}}>Kamal Haddad</h2>
                    <h4 style={{color: 'grey'}}>Programmer</h4>
                    <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
                    <h5>Address</h5>
                    <p>804 Parker Street, Boston, MA, 02120</p>
                    <h5>Phone</h5>
                    <p>(508) 335-7189</p>
                    <h5>Email</h5>
                    <p>kamalmhaddad4@gmail.com</p>
                    <h5>Website</h5>
                    <p>www.kamal-haddad.github.io</p>
                    <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
                    </Cell>
                    <Cell style={{width: '60%'}}>
                        <iframe src="https://drive.google.com/file/d/1CQIFGe3HH0YpV_odtyyUilitkNcWSeL3/preview" style={{width: '100%', height: '100%'}}/>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Resume;