import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class About extends Component {
    render() {
        return(
            <div className="about-heading">
                <h2>About me</h2>
                <p>
                    Hello, my name is Kamal Haddad and I'm a Lebanese-American computer 
                    programmer who likes to solve challenging problems involving complex distributed systems handling petabytes of data.
                    My goal is to find better ways to understand data and get us one step closer to achieving AGI.
                    I earned my Bachelor's degree in Electrical and Computer Engineering from the American University of Beirut and I am
                    currently pursuing my Master's degree in Computer Science at Northeastern University. 
                    <br/><br/>
                    I enjoy competitive programming, building computer systems and pursuing DIY projects
                    as well as  traveling.
                    <br/><br/>
                    Please find some of the projects <Link to="/projects">here</Link> and my resume <Link to="/resume">here</Link>.
                </p>
            </div>
        )
    }
}

export default About;