import React, { Component } from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardActions, CardTitle, CardText, Button} from 'react-mdl';

class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {activeTab : 0};
    }

    toggleCategories = () => {
        if(this.state.activeTab === 0) {
            return(
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: 'black', height: '176px', background: 'url(https://sdtimes.com/wp-content/uploads/2018/03/cpppp-490x490.png) center  / cover'}}>
                            DRAM Simulator
                        </CardTitle>
                        <CardText>
                            DRAM Timing and Performance Simulator
                        </CardText>
                        <CardActions border>
                            <Button colored><a href="https://github.com/tukl-msd/DRAMSpec">GitHub</a></Button>
                        </CardActions>
                    </Card>
                </div>
            )
        } else if(this.state.activeTab === 1) {
            return(
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: 'black', height: '176px', background: 'url(https://assets.exercism.io/tracks/java-bordered-turquoise.png) center  / cover'}}>
                            Course Registration System
                        </CardTitle>
                        <CardText>
                            Java Desktop App for course registration
                        </CardText>
                        <CardActions border>
                            <Button colored><a href="https://github.com/kamalhaddad/PreRegSystem">GitHub</a></Button>
                        </CardActions>
                    </Card>
                </div>            
            )
        } else if(this.state.activeTab === 2) {
            return(
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: 'black', height: '176px', background: 'url(https://yt3.ggpht.com/a/AGF-l78OmZxme65XB4lAq3jgh8cAx5qZ-4-eYZiY-g=s288-c-k-c0xffffffff-no-rj-mo) center  / cover'}}>
                            Self-balancing quadcopter
                        </CardTitle>
                        <CardText>
                            Self-balancing quadcopter simulation in Matlab
                        </CardText>
                        <CardActions border>
                            <Button colored><a href="https://github.com/kamalhaddad/ControlD-AProject">GitHub</a></Button>
                        </CardActions>
                    </Card>
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://hackernoon.com/drafts/tv392249.png) center  / cover'}}>
                            Pedestrian Detection
                        </CardTitle>
                        <CardText>
                            Pedestrian Detection for Self-driving Vehicles
                        </CardText>
                        <CardActions border>
                            <Button colored><a href="https://github.com/amoussawi/caffe">GitHub</a></Button>
                            <Button colored><a href="https://arxiv.org/abs/1809.05879">Paper</a></Button>
                            <Button colored><a href="https://www.youtube.com/watch?v=iKIW5Q0XAcg">Live Demo</a></Button>
                        </CardActions>
                    </Card>
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://hackernoon.com/drafts/tv392249.png) center  / cover'}}>
                            TensorFlow tutorials
                        </CardTitle>
                        <CardText>
                            TensorFlow tutorials via Jupyter Notebooks
                        </CardText>
                        <CardActions border>
                            <Button colored><a href="https://github.com/kamalhaddad/tensorflowTutorials">GitHub</a></Button>
                        </CardActions>
                    </Card>
                </div>             
                )
        } else if(this.state.activeTab === 3) {
            return(
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://miro.medium.com/max/3840/1*tRp3G6ffXDMDVOvMebzYgg.jpeg) center  / cover'}}>
                            Voting App
                        </CardTitle>
                        <CardText>
                            Voting App using MERN stack
                        </CardText>
                        <CardActions border>
                            <Button colored><a href="https://github.com/kamalhaddad/voting-app">GitHub</a></Button>
                        </CardActions>
                    </Card>
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://miro.medium.com/max/1500/1*y6C4nSvy2Woe0m7bWEn4BA.png) center  / cover'}}>
                            My Portfolio Website
                        </CardTitle>
                        <CardText>
                            Basically This Website
                        </CardText>
                        <CardActions border>
                            <Button colored><a href="https://github.com/kamalhaddad/kamalhaddad.github.io">GitHub</a></Button>
                        </CardActions>
                    </Card>

                </div>
            ) 
        }
    }

    render() {
        return(
            <div className="category-tabs">
                <Tabs activeTab={this.state.activate} onChange={(tabId) => this.setState({activeTab: tabId})} ripple>
                    <Tab>C++</Tab>
                    <Tab>Java</Tab>
                    <Tab>ML/AI</Tab>
                    <Tab>Web</Tab>
                </Tabs>
                <Grid>
                    <Cell col={12}>
                        {this.toggleCategories()}
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Projects;