import React, { Component } from 'react';
import {Grid, Cell, List, ListItem, ListItemContent} from 'react-mdl';

class Contact extends Component {
    render() {
        return(
            <div className="contact-body">
                <Grid className="contact-grid">
                    <Cell cols={6}>
                        <h2>Kamal Haddad</h2>
                        <img src="https://cdn1.imggmi.com/uploads/2019/12/13/27266257b187362f1aa0ee2e6f745e2f-full.png" alt="avatar" style={{height: '250px'}}/>
                        <p style={{width: '75%', margin: 'auto', paddingTop:'1em'}}>
                        Looking forward to hearing from you!
                        </p>
                    </Cell>
                    <Cell cols={6}>
                        <h2>Contact Me</h2>
                        <hr/>
                        <div className="contact-list">
                            <List>
                                <ListItem>
                                    <ListItemContent style={{fontSize: '20px'}}>
                                        <i className="fa fa-phone-square" aria-hidden="true"/>
                                        (508) 335-7189
                                    </ListItemContent>
                                </ListItem>

                                <ListItem>
                                    <ListItemContent style={{fontSize: '16px'}}>
                                        <i className="fa fa-envelope-square" aria-hidden="true"/>
                                        kamalmhaddad4@gmail.com
                                    </ListItemContent>
                                </ListItem>

                                <ListItem>
                                    <ListItemContent style={{fontSize: '20px'}}>
                                        <i className="fa fa-skype" aria-hidden="true"/>
                                        kamalhaddad04
                                    </ListItemContent>
                                </ListItem>
                            </List>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Contact;